/**
 * Estilização do app
 */
.reset-button,
button,
.navbar-toggle {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.clearfix:after,
.navbar-control:after,
.post-summary.-internal:after {
  content: "";
  display: table;
  clear: both;
}
@font-face {
  font-family: 'ebenezer';
  src: url("../fonts/ebenezer.eot");
  src: url("../fonts/ebenezer.eot#iefix") format('embedded-opentype'), url("../fonts/ebenezer.woff") format('woff'), url("../fonts/ebenezer.ttf") format('truetype'), url("../fonts/ebenezer.svg#ebenezer") format('svg');
  font-weight: normal;
  font-style: normal;
}
@import url("https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans:400,700");
[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "ebenezer";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  font-size: 120%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(127,127,127,0.3);
}
* {
  box-sizing: border-box;
}
html {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}
body {
  font-size: 1rem;
}
::selection {
  background: #7d965d;
  color: #fff;
}
:focus {
  border: 0;
  outline: 2px auto #333;
}
h1,
h2 {
  margin-top: 0;
  font-family: 'Montserrat';
  font-weight: 700;
  text-transform: uppercase;
}
h1 {
  margin-bottom: 30px;
  font-size: 24px;
  text-align: center;
}
h2 {
  font-size: 20px;
  margin-bottom: 30px;
}
p {
  font-size: 16px;
  line-height: 160%;
  margin-bottom: 30px;
}
ul {
  padding: 0;
  margin: 0;
}
a {
  color: #7e9659;
  text-decoration: none;
  transition: 0.2s color linear;
}
a:hover,
a:focus,
a:active {
  text-decoration: underline;
}
li {
  list-style: none;
  margin-bottom: 0;
}
object {
  position: absolute;
  left: -1000px;
}
button {
  transition-duration: 0.2s;
  transition-timing-function: linear;
  transition-property: color, background;
}
blockquote {
  padding: 10px 20px;
  margin: 0 0 20px;
  font-size: 1.25rem;
  border-left: 5px solid #848973;
}
blockquote ol:last-child,
blockquote p:last-child,
blockquote ul:last-child {
  margin: 0;
}
._img-responsive {
  max-width: 100%;
  display: block;
  height: auto;
}
._text-right {
  text-align: right;
}
._overflow-hidden {
  overflow-x: hidden;
}
._section-site {
  margin-top: 25px;
}
._center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
._btn-margin {
  margin-top: 40px;
}
._margin-left-25 {
  margin-left: 25px;
}
._table_overflow {
  width: 100%;
}
@media (max-width: 767px) {
  ._table_overflow {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
}
@media (min-width: 1200px) {
  .__invisible-lg {
    display: none !important;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .__invisible-md {
    display: none !important;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .__invisible-sm {
    display: none !important;
  }
}
@media (min-width: $screen-xs-min) and (max-width: 767px) {
  .__invisible-vs {
    display: none !important;
  }
}
@media (max-width: $screen-xs) {
  .__invisible-xs {
    display: none !important;
  }
}
.accessibility-list > .item {
  display: inline-block;
}
.accessibility-list > .item > .button {
  padding-right: 0;
  padding-left: 20px;
  font-size: 1.25rem;
  font-weight: 700;
  color: #fff;
  background: transparent;
  border: 0;
}
.accessibility-list > .item > .button:hover,
.accessibility-list > .item > .button:focus,
.accessibility-list > .item > .button:active {
  color: #848973;
  outline: none;
}
.accessibility-list > .item > .button .icon:before {
  font-size: 100%;
}
.accessibility-list > .item:first-child > .button {
  padding-left: 0;
}
.accessibility-list > .item:last-child > .button {
  padding-left: 10px;
  font-weight: normal;
}
.bread-site {
  padding: 0;
  margin: 0;
}
.bread-site > .item {
  font-size: 0.875rem;
  list-style: none;
  display: inline-block;
}
.bread-site > .item > .link {
  color: #fff;
}
.bread-site > .item > .link .php-active {
  font-weight: bold;
  cursor: text;
}
.bread-site > .item > .link .php-active:hover,
.bread-site > .item > .link .php-active:focus,
.bread-site > .item > .link .php-active:active {
  text-decoration: none;
}
.bread-site > .item > .icon:before {
  color: #fff;
  transform: rotate(-90deg);
}
.btn-default {
  padding: 8px 35px;
  font-weight: bold;
  color: #7e9659;
  background-color: transparent;
  border-radius: 20px;
  border: 2px solid;
  border-color: #7e9659;
  transition: 0.2s all linear;
  display: inline-block;
}
.btn-default:hover,
.btn-default:focus,
.btn-default:active {
  background: #7e9659;
  color: #fff;
  outline: 0;
}
a.btn-default {
  text-decoration: none;
}
a.btn-default:hover,
a.btn-default:focus,
a.btn-default:active {
  text-decoration: none;
}
input.btn-default:hover,
input.btn-default:focus,
input.btn-default:active {
  cursor: pointer;
}
.btn-default.-danger {
  color: #d86565;
  border-color: #d86565;
}
.btn-default.-danger:hover,
.btn-default.-danger:focus,
.btn-default.-danger:active {
  background: #d86565;
  color: #fff;
}
@media (min-width: 1200px) {
  .container-nav {
    margin-top: 90px;
    overflow: hidden;
  }
}
.embed-responsive {
  height: 0;
  padding-bottom: 56.25%;
  display: block;
  position: relative;
  overflow: hidden;
}
.embed-responsive > .media {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
.footer-site {
  padding: 15px 0;
  margin-top: 90px;
  background-color: #e1e3da;
}
.footer-site .phrase {
  position: relative;
  margin-left: 15px;
}
.footer-site .phrase:before,
.footer-site .phrase:after {
  font-family: 'fontAwesome';
  position: absolute;
}
.footer-site .phrase:before {
  content: '\f10d';
  top: -15px;
  left: -15px;
}
.footer-site .phrase:after {
  content: '\f10e';
  bottom: -15px;
}
.footer-site .facebook {
  margin-top: 0;
}
.footer-site .facebook a {
  color: inherit;
}
.footer-site .location {
  margin-top: 50px;
  margin-bottom: 0;
}
@media (min-width: 992px) {
  .footer-site .facebook,
  .footer-site .location {
    text-align: right;
  }
}
.control-label {
  font-weight: 700;
  color: #7e9659;
}
.form-control {
  display: block;
  width: 100%;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
  background-color: #fff;
  background-image: none;
  border: 1px solid 1px #848973 solid;
  border-radius: 0;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}
.form-control:not(textarea) {
  height: 34px;
}
.form-control:focus {
  border-color: #66afe9;
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.075), 0 0 8px rgba(102,175,233,0.6);
}
::-webkit-input-placeholder {
  color: #333;
  opacity: 1;
}
::-moz-placeholder {
  color: #333;
  opacity: 1;
}
:-ms-input-placeholder {
  color: #333;
  opacity: 1;
}
.form-search {
  margin-bottom: 20px;
}
.input-group {
  display: table;
  position: relative;
}
.input-group.-full {
  width: 100%;
}
.input-group > .addon {
  width: 1%;
  display: table-cell;
}
.input-group > .addon > .button {
  padding: 6px 12px;
  font-size: 14px;
  color: #333;
  line-height: 1.42857143;
  border-radius: 4px;
  border: 1px #848973 solid;
  border-left: 0;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  transition: 0.2s all linear;
  display: inline-block;
}
.input-group > .addon > .button:before {
  transition: 0.2s transform linear;
}
.input-group > .addon > .button:hover,
.input-group > .addon > .button:focus,
.input-group > .addon > .button:active {
  outline: 0;
  color: #1a1a1a;
}
.input-group > .addon > .button:hover:before,
.input-group > .addon > .button:focus:before,
.input-group > .addon > .button:active:before {
  transform: scale(1.2);
  transform-origin: center;
}
.header-site {
  background-color: #e1e3da;
  padding-top: 15px;
}
.info-icon > .icon,
.info-icon > .title {
  text-align: center;
  margin-bottom: 20px;
}
.info-icon > .icon {
  font-size: 140px;
  display: block;
}
.info-icon > .title {
  font-size: 16px;
  font-family: 'Montserrat';
}
.internal-banner {
  background-color: #848973;
  min-height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.internal-banner.-image {
  background-size: cover;
  background-repeat: no-repeat;
}
.internal-banner.-image .title,
.internal-banner.-image .description {
  background-color: rgba(126,150,89,0.75);
}
.internal-banner .title {
  padding: 0 15px;
  margin: 0 0 40px 0;
  font-size: 3.75rem;
  color: #fff;
}
.internal-banner .description {
  padding: 0 15px;
  font-size: 1.25rem;
  color: #fff;
}
.internal-content {
  margin-top: 20px;
/* Provisório */
}
.internal-content h1 {
  font-size: 1.75rem;
}
.internal-content h2 {
  text-transform: none;
  font-size: 1.25rem;
}
.internal-content p {
  font-size: 1rem;
}
.internal-content figure {
  width: auto !important;
  height: auto !important;
  margin: 0;
}
.internal-content figure img {
  width: auto !important;
  height: auto;
  max-width: 100%;
  display: block;
}
.internal-content figure figcaption {
  padding: 10px;
  font-family: montserrat;
  font-size: 0.75rem;
  font-weight: 700;
  color: #fff;
  background: #7e9659;
}
.jump-to {
  position: absolute;
  z-index: 999999;
}
.jump-to > .link {
  width: 1px;
  height: 1px;
  padding: 5px;
  left: -10000px;
  background-color: #7d965d;
  position: absolute;
  overflow: hidden;
}
.jump-to > .link:focus,
.jump-to > .link:active,
.jump-to > .link:hover {
  width: auto;
  height: auto;
  left: 0;
  color: #fff;
  text-decoration: none;
  position: static;
  overflow: visible;
}
.list-categories {
  margin: 0;
}
.list-categories > .item {
  padding: 0;
  margin: 0 10px 0 0;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  display: inline-block;
}
.list-categories > .item:before {
  content: '•';
  margin-right: 5px;
  font-size: 100%;
  display: inline;
  position: static;
}
.list-events.-border > .item {
  border-bottom: 1px solid #7d965d;
  margin-bottom: 20px;
}
.list-events.-bordergray > .item {
  border-bottom: 1px solid #b3b3b3;
  margin-bottom: 15px;
}
.list-events.-article > .item > .text {
  font-size: 0.875rem;
  margin: 0 0 15px;
}
.list-events > .item > .date {
  font-size: 0.75rem;
  display: block;
}
.list-events > .item > .link {
  width: 100%;
  padding-bottom: 15px;
  font-weight: bold;
  color: #7d965d;
  text-decoration: none;
  display: inline-block;
}
.list-events > .item > .link:hover,
.list-events > .item > .link:focus,
.list-events > .item > .link:active {
  color: #586941;
  text-decoration: none;
  outline: 0;
}
.list-events > .item > .link > .icon {
  margin-left: -7px;
}
.internal-content ul,
.internal-content ol,
.list-site {
  margin-bottom: 20px;
  padding: 0;
  display: block;
}
.internal-content ul > li,
.internal-content ol > li,
.list-site > li {
  padding-left: 20px;
  margin-bottom: 15px;
  position: relative;
}
.internal-content ul > li::before,
.internal-content ol > li::before,
.list-site > li::before {
  content: '\2022';
  max-width: 0;
  max-height: 0;
  left: 0;
  font-size: 160%;
  line-height: 1.45rem;
  display: block;
  position: absolute;
}
.internal-content ul.-green > li::before,
.internal-content ol.-green > li::before,
.list-site.-green > li::before {
  color: #7d965d;
}
.internal-content ul.-check > li,
.internal-content ol.-check > li,
.list-site.-check > li {
  padding-left: 25px;
}
.internal-content ul.-check > li::before,
.internal-content ol.-check > li::before,
.list-site.-check > li::before {
  content: '\e80f';
  font-size: 100%;
  line-height: 1.5;
}
.internal-content ul.-simple > li,
.internal-content ol.-simple > li,
.list-site.-simple > li {
  padding-left: 0;
  margin-bottom: 5px;
}
.internal-content ul.-simple > li::before,
.internal-content ol.-simple > li::before,
.list-site.-simple > li::before {
  display: none;
}
.list-videos > .more {
  padding: 10px;
  text-align: center;
  font-weight: bold;
  border-top: 1px solid #7e9659;
  display: block;
}
.list-videos > .more:hover,
.list-videos > .more:focus,
.list-videos > .more:active {
  outline: 0;
}
.logo-site {
  display: inline-block;
  padding-bottom: 15px;
}
.message-site {
  margin: 10px 0;
  font-size: 12px;
  font-weight: 700;
  color: #7e9659;
}
.owl-buttons {
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  margin: auto;
}
.owl-buttons .icon {
  font-size: 70px;
  color: #fff;
}
.owl-buttons .icon:hover,
.owl-buttons .icon:focus,
.owl-buttons .icon:active,
.owl-buttons .icon:active:focus {
  color: #7d965d;
  transition: all 0.1s ease-in-out;
  opacity: 0.8;
}
.owl-buttons .owl-prev,
.owl-buttons .owl-next {
  top: -35px;
  position: absolute;
}
.owl-buttons .owl-prev {
  left: 0;
}
.owl-buttons .owl-next {
  right: 0;
}
.owl-pagination {
  width: 100%;
  position: absolute;
  bottom: 10px;
  text-align: center;
}
.owl-pagination .owl-page {
  display: inline-block;
  margin-right: 10px;
}
.owl-pagination .owl-page span {
  background-color: #fff;
  width: 15px;
  height: 15px;
  display: inline-block;
  border-radius: 50%;
}
.owl-pagination .owl-page:hover span,
.owl-pagination .owl-page.active span {
  background-color: #7d965d;
}
.nav-site {
  font-size: 0;
}
.nav-site > .item > .link {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  display: block;
}
@media (min-width: 1200px) {
  .nav-site {
    text-align: right;
  }
  .nav-site > .item {
    display: inline-block;
    margin-right: 15px;
  }
  .nav-site > .item:last-child {
    margin-right: 0;
  }
  .nav-site > .item > .link {
    padding-bottom: 15px;
  }
  .nav-site > .item > .link:after {
    content: '';
    width: 100%;
    height: 6px;
    bottom: 0;
    left: 0;
    background-color: #848973;
    transform: translateY(7px);
    transform-origin: bottom;
    transition: 0.2s all linear;
    position: absolute;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
    color: #848973;
    outline: 0;
  }
  .nav-site > .item > .link:hover:after,
  .nav-site > .item > .link:focus:after,
  .nav-site > .item > .link:active:after {
    transform: translateY(0);
  }
}
.navbar-control {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.navbar-control:not(.-close) {
  margin-top: 75px;
  margin-left: 20px;
}
.navbar-control > .text {
  margin: 0 5px 0 0;
  font-size: 14px;
  color: #333;
}
.navbar-toggle {
  width: 52px;
  height: 40px;
  padding: 9px 10px;
  position: relative;
}
.navbar-toggle > .icon-bar {
  display: block;
  width: 30px;
  height: 3px;
  border-radius: 4px;
  background-color: #333;
  transition: all 0.2s linear;
}
.navbar-toggle > .icon-bar:nth-child(2) {
  opacity: 1;
}
.navbar-toggle .icon-bar + .icon-bar {
  margin-top: 4px;
}
.pagination-site {
  padding: 0;
  margin: 0;
  font-size: 0;
  text-align: center;
}
.pagination-site > .item {
  padding: 0;
  margin-bottom: 10px;
  list-style: none;
  display: inline-block;
  text-align: center;
}
.pagination-site > .item.-alone,
.pagination-site > .item > .link {
  width: 40px;
  margin-right: 10px;
  font-weight: bold;
  font-size: 0.875rem;
  line-height: normal;
  padding: 12px 0;
  background-color: #e1e3da;
  display: inline-block;
}
.pagination-site > .item > .link {
  color: #333;
}
.pagination-site > .item > .link:hover,
.pagination-site > .item > .link:focus,
.pagination-site > .item > .link:active,
.pagination-site > .item > .link.-is-active {
