.post-summary {
  margin-bottom: 20px;
}
.post-summary .date {
  font-size: 12px;
  font-weight: bold;
}
.post-summary .title {
  margin: 0;
  font-family: 'Montserrat';
  font-size: 20px;
  font-weight: bold;
  text-align: left;
  text-transform: uppercase;
  color: #7e9659;
}
.post-summary .link {
  text-decoration: underline;
}
.post-summary.-internal {
  margin-bottom: 50px;
  border-bottom: 1px solid #848973;
