}
.post-summary.-internal .title {
  font-size: 1.5rem;
}
.post-summary.-internal .summary {
  margin-top: 15px;
}
.post-summary.-single .date {
  font-size: 0.75rem;
}
.post-summary.-single .title {
  font-size: 1.625rem;
}
.sr-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.table-site {
  width: 100%;
  border-collapse: collapse;
}
.table-site tr:last-child td {
  border-bottom: 0;
}
.table-site th {
  background-color: #7e9659;
  color: #fff;
}
.table-site td {
