.table-site th,
.table-site td {
  padding: 10px;
}
.util-bar {
  background: #333;
  padding: 10px 0;
}
.video-item {
  margin-bottom: 15px;
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-start;
}
.video-item > .link {
  margin-right: 5px;
}
.video-item > .content > .title {
