}
.video-item > .content > .date {
  font-size: 12px;
}
.video-site {
  margin-bottom: 20px;
  display: block;
  position: relative;
}
.video-site .embed {
  display: none;
}
.video-site .embed.js-visible {
  display: block;
}
.video-site > .link > .image,
.video-site > .image {
  width: 100%;
}
.video-site > .link {
  display: block;
  position: relative;
}
.video-site > .link.js-hidden,
.video-site > .label.js-hidden {
  display: none;
}
.video-site > .link .icon {
  content: '\e80b';
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.2);
  color: #fff;
  font-size: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 2;
}
.video-site > .link .icon:before {
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  transition: all linear 0.4s;
}
.video-site > .link .icon.js-active:before {
  transform: scale(1.5);
  opacity: 0;
  visibility: hidden;
  transition: all linear 0.4s;
}
.video-site > .label {
  width: 100%;
  padding: 10px 15px;
  bottom: 0;
  left: 0;
  color: #fff;
  text-align: center;
  background-color: rgba(0,0,0,0.7);
}
.video-site > .label > .description {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}
.video-site > .label > .description > .title {
  margin: 0;
  font-size: 20px;
  font-weight: 700;
  font-family: 'Open Sans';
  text-transform: none;
  text-align: left;
  line-height: normal;
}
.video-site > .label .link {
  color: #fff;
}
.video-site > .label .link:hover,
.video-site > .label .link:focus,
.video-site > .label .link:active {
  color: #fff;
  text-decoration: none;
}
@media (min-width: 768px) {
  .video-site > .label {
    position: absolute;
    z-index: 3;
  }
}
.video-site.-article > .link {
  display: inline-block;
}
.video-site.-article > .link .icon {
  font-size: 60px;
}
@media (max-width: 1199px) {
  .video-site > .link .icon:before {
    line-height: 2;
  }
}
@media (max-width: 519px) {
  .video-site > .link .icon {
    font-size: 80px;
  }
