.input-group .form-control {
  display: table-cell;
  border-right: 0;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.form-group.-error .control-label {
  color: #d86565;
}
.form-group.-error .form-control {
  border-color: #d86565;
}
.form-search .form-control,
.form-search .input-group > .addon > .button {
  color: #ababab;
  border-color: #ababab;
}
.wrapper-site {
  transition: 0.4s transform ease-in-out;
}
.wrapper-site.js-active-menu {
  height: 100%;
  transition: 0.4s transform ease-in-out;
  transform: translateX(-200px);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(1) {
  transform: rotate(45deg);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(2) {
  width: 0;
  height: 0;
  margin: 0;
  opacity: 0;
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(3) {
  margin-top: -3px;
  transform: rotate(-45deg);
}
@media (max-width: 1199px) {
  .js-ie .wrapper-site.js-active-menu .nav-site {
    transition: 0.4s transform ease-in-out;
    transform: translateX(0px);
    opacity: 1;
  }
  .js-ie .nav-site {
    overflow-x: hidden;
    overflow-y: auto;
    transition: 0.4s transform ease-in-out;
  }
  .js-ie .nav-site._no-opacity {
    opacity: 0;
  }
  .nav-site {
    width: 200px;
    height: 100%;
    min-height: 100vh;
    min-height: 100vmax;
    top: 0;
    right: 0;
    background-color: #e1e3da;
    transform: translateX(200px);
    visibility: hidden;
    position: fixed;
  }
  .nav-site.js-active {
    visibility: visible;
  }
  .nav-site > .item > .link {
    padding: 15px;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
